import React from "react";
import projectTempStyle from "../Styles/ProjectTemplate.module.css";

const ProjectTemplate = ({
  img,
  title,
  description,
  // techStacks,
  techStack1,
  techStack2,
  techStack3,
  techStack4,
  githubLink,
  deployLink,
}) => {
  // const techStacksList = techStacks.map((item, i) => {
  //   return (
  //     <div key={item.id}>
  //       <img src={item.path} alt={item.alt} />
  //       {/* <p>{item}</p> */}
  //     </div>
  //   );
  // });

  return (
    <div className={projectTempStyle.project}>
      <div className={projectTempStyle.leftBox}>
        {/* image */}
        <div className={projectTempStyle.imgDiv}>
          <img src={img} alt="" />
        </div>
      </div>
      <div className={projectTempStyle.rightBox}>
        {/* title */}
        <div className={projectTempStyle.titleDiv}>
          <p>{title}</p>
        </div>
        {/* description */}
        <div className={projectTempStyle.descriptionDiv}>
          <p>{description}</p>
        </div>
        {/* tech stacks */}
        <div className={projectTempStyle.techStacksDiv}>
          {/* <p>{techStacks1}</p> */}
          {techStack1 ? (
            <div>
              <img src={techStack1[1]} alt={techStack1[0]} />
            </div>
          ) : (
            ""
          )}
          {techStack2 ? (
            <div>
              <img src={techStack2[1]} alt={techStack2[0]} />
            </div>
          ) : (
            ""
          )}
          {techStack3 ? (
            <div>
              <img src={techStack3[1]} alt={techStack3[0]} />
            </div>
          ) : (
            ""
          )}
          {techStack4 ? (
            <div>
              <img src={techStack4[1]} alt={techStack4[0]} />
            </div>
          ) : (
            ""
          )}
        </div>
        {/*  */}
        {/* {techStacksList} */}
        {/*  */}
        {/* links */}
        <div className={projectTempStyle.btnDiv}>
          <a href={githubLink} target="_blank" rel="noopener noreferrer">
            <button className={projectTempStyle.btnGit}>
              <img src="./images/projects/github.png" alt="" />
              GitHub
            </button>
          </a>
          <a href={deployLink} target="_blank" rel="noopener noreferrer">
            <button className={projectTempStyle.btnLink}>
              <img src="./images/projects/link.png" alt="" /> Deployed Link
            </button>
          </a>
        </div>
      </div>
    </div>
  );
};

export default ProjectTemplate;

