import React from "react";
import iconTempStyle2 from "../Styles/IconTemplate2.module.css";

const IconTemplate2 = ({ name, width, src }) => {
  return (
    <div className={iconTempStyle2.iconTemp}>
      <div>
        <img
          className={iconTempStyle2.iconImg}
          width={width}
          src={src}
          alt={name}
        />
        <p className={iconTempStyle2.iconTitle}>{name}</p>
      </div>
    </div>
  );
};

export default IconTemplate2;
